import '../pages/index.css';
//import { initialCards } from './cards.js';
import { createCard, addLikeCard } from './card.js';
import { openModal, closeModal } from './modal.js';
import { enableValidation, clearValidation } from './validation.js';
import { getInfoProfile, getInitialCards, editProfileInfo, addNewCard, deleteUserCard, updateAvatarUser } from './api.js';


const cardsContainer = document.querySelector('.places__list');

const profileEditButton = document.querySelector('.profile__edit-button');
const addButton = document.querySelector('.profile__add-button');

const popupEdit = document.querySelector('.popup_type_edit');
const popupAvatar = document.querySelector('.popup_type_avatar');
const popupNewCard = document.querySelector('.popup_type_new-card');
const popupCloseButtons = document.querySelectorAll('.popup__close');

const formProfile = popupEdit.querySelector('.popup__form');
const nameInput = formProfile.querySelector('.popup__input_type_name');
const jobInput = formProfile.querySelector('.popup__input_type_description');

const formCard = popupNewCard.querySelector('.popup__form');
const inputCardName = formCard.querySelector('.popup__input_type_card-name');
const inputCardUrl =  formCard.querySelector('.popup__input_type_url');

const profileTitle = document.querySelector('.profile__title');
const profileDscription = document.querySelector('.profile__description');

const popupImages = document.querySelector('.popup_type_image');
const imageLink = popupImages.querySelector('.popup__image');
const imageName = popupImages.querySelector('.popup__caption');

const popupDeleteCard = document.querySelector('.popup_delete-image');
const popupDeleteCardButton = popupDeleteCard.querySelector('.popup__button');

const profileAvatar = document.querySelector('.profile__image');
const formAvatar = popupAvatar.querySelector('.popup__form');
const inputAvaUrl = formAvatar.querySelector('.popup__input_type_url');

let userId = '';
const cardId = null;

const  validationConfig = {
  formSelector: '.popup__form',
  inputSelector: '.popup__input',
  submitButtonSelector: '.popup__button',
  inactiveButtonClass: 'popup__button_disabled',
  inputErrorClass: 'popup__input_type_error',
  errorClass: 'popup__error_visible'
};


// загрузка данных
function renderLoading(isLoading, form) {
  const activeButton = form.querySelector('.popup__button');
  activeButton.textContent = isLoading ? "Сохранение..." : "Сохранить";
};


// открытие модального окна картинки
function openPopupImage(cardData) {
  openModal(popupImages);
  imageLink.src = cardData.target.src;
  imageLink.alt = cardData.target.alt;
  imageName.textContent = cardData.target.alt;
};

cardsContainer.addEventListener('click', openPopupImage);


// открытие модального окна редактирования профиля (карандаш)
profileEditButton.addEventListener('click', (evt) => {
  evt.preventDefault();
  openModal(popupEdit);
  nameInput.value = profileTitle.textContent;
  jobInput.value = profileDscription.textContent;
  clearValidation(popupEdit, validationConfig);
});


// редактирование данных профиля в модальном окне
function processProfileSubmit(evt) {
  evt.preventDefault();

  renderLoading(true, formProfile);

  editProfileInfo({
    name: nameInput.value,
    about: jobInput.value,
  })
    .then((res) => {
      profileTitle.textContent = res.name;
      profileDscription.textContent = res.about;

      closeModal(popupEdit);
    })
    .catch((error) => {
      console.log(error);
    })
    .finally(() => {
      renderLoading(false, formProfile);
    })
};


// сохранение отредактированных данных в профиле
formProfile.addEventListener('submit', processProfileSubmit);


// модальное окно изменения аватара
profileAvatar.addEventListener('click', (evt) => {
  evt.preventDefault();
  openModal(popupAvatar);
  clearValidation(popupAvatar, validationConfig);
});


// установка аватара в модальном окне
function processAvatarSubmit(evt) {
  evt.preventDefault();

  renderLoading(true, formAvatar);
  
  updateAvatarUser(inputAvaUrl.value)
    .then((res) => {
      profileAvatar.style.backgroundImage = `url(${res.avatar})`

      closeModal(popupAvatar)
    })
    .catch((error) => {
      console.log(error);
    })
    .finally(() => {
      renderLoading(false, formAvatar);
    })
};


formAvatar.addEventListener('submit', processAvatarSubmit);


// открытие модального окна добавления картинки (+)
addButton.addEventListener('click', (evt) => {
  evt.preventDefault();
  openModal(popupNewCard);
  clearValidation(popupNewCard, validationConfig);
});


// получаем новую картинку в список картинок
 function processNewCardSubmit(evt) {
  evt.preventDefault();

  const newCard = {
    name: inputCardName.value,
    link: inputCardUrl.value,
  }
  renderLoading(true, formCard);

  addNewCard(newCard)
    .then((res) => {
      cardsContainer.prepend(createCard(
        res, 
        res.owner._id, 
        openPopupImage,
        deleteCard,  
        addLikeCard,
      ));
      
      closeModal(popupNewCard);
    })
  .catch((error) => {
    console.log(error);
  })
  .finally(() => {
    renderLoading(false, formCard);
  })
};


formCard.addEventListener('submit', processNewCardSubmit);


function openModalDeleteCard(cardData) {
  openModal(popupDeleteCard);
  popupDeleteCardButton.dataset.cardId = cardData._id;
};


function processDeleteCardClick(evt) {
  evt.preventDefault();

  // Присваиваем ID удаляемой карточки в попап подтверждения
  cardId = popupDeleteCardButton.dataset.cardId;
  // Удаляем с сервера
  deleteUserCard(cardId)
    .then(() => {
      // Через ID на карточках в списке HTML удаляем со страницы
      const deleteCard = document.getElementById(cardId);

      deleteCard.remove();
      popupDeleteCardButton.dataset.cardId = "";
      closePopup(popupDeleteCard);
    })
    .catch((error) => {
      console.log(error);
    });
};


popupDeleteCardButton.addEventListener("click", () => processDeleteCardClick(cardId, popupDeleteCard));


// закрытие модальных окон (All)
popupCloseButtons.forEach((item) => {
  item.addEventListener('click', () => {
    const popupToClose = item.closest('.popup');
    closeModal(popupToClose);
  });
});


Promise.all([getInfoProfile(), getInitialCards()])
  .then(([user, newCard]) => {
    profileTitle.textContent = user.name;
    profileDscription.textContent = user.about;
    profileAvatar.style.backgroundImage = `url(${user.avatar})`;
    userId = newCard._id;
    newCard.forEach((cardData) => {
      cardsContainer.append(
        createCard(cardData, openPopupImage, openModalDeleteCard, addLikeCard, userId));
      });
  })
  .catch((error) => {
    console.log(error);
  });


  enableValidation(validationConfig);
